body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: #191919;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

@font-face {
	font-family: 'TitleFont';
	src: local('TitleFont'),
		url(./assets/fonts/ChopinScript.ttf) format('truetype');
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: 'TitleFont2';
	src: local('TitleFont2'),
		url(./assets/fonts/JosefinSans-Regular.ttf) format('truetype');
	font-weight: normal;
	font-style: normal;
}

.horizontal-navbar {
	margin: 0px auto;
	width: 630px;
	top: 0;
	position: sticky;
	display: flex;
	align-items: baseline;
	background-image: linear-gradient(to right, #e7be7f, #906e49);
	border-radius: 10px;
}

.hidden {
	visibility: hidden;
	transition: all 0.5s;
	transform: translateY(-100%);
}

.active {
	visibility: visible;
	transition: all 0.5s;
}

.nav-links {
	/* Styles for the navigation link container */
	display: flex;
	list-style: none;
	padding: 0;
	margin: 0;
}

.nav-link {
	/* Styles for individual navigation links */
	font-family: TitleFont2;
	padding: 5px;
	margin-right: 15px;
	text-decoration: none;
	text-align: center;
	font-size: 20px;
}

.nav-link:hover {
	color: goldenrod !important;
}

.full-screen-background {
	border: 150px solid transparent;
	border-image: url('./assets/img/bg_img.jpg') 500 stretch;
	overflow-y: scroll;
	overflow-x: hidden;
}

.title {
	font-family: TitleFont;
	font-size: 84px;
	text-shadow: 2px 2px 2px gray;
}

.sub-title {
	font-family: TitleFont2;
	font-size: 20px;
}

.iframe-wrapper {
	filter: invert(1);
}

.rsvp-form {
	filter: hue-rotate(189.73deg) saturate(18.61%) brightness(96.86%);
}

.text-bold {
	font-weight: bolder;
}

h2 {
	font-size: 48px !important;
}

h3 {
	font-size: 32px !important;
}

.header {
	margin-top: 250px;
}

.story {
	margin-top: 500px;
}

.wed-info {
	margin-top: 500px;
	margin-bottom: 500px;
}

.venue-image {
	width: 35rem;
	height: 35rem;
	margin: 0;
	padding: 0;
}

@media (min-width: 769px) and (max-width: 1023px) {
	.venue-image {
		width: 25rem;
		height: 25rem;
	}

	.horizontal-navbar {
		left: 0;
		margin: 0;
		width: 30px;
		background-image: none;
		border-radius: 0;
	}
}

@media (max-width: 768px) {
	.horizontal-navbar {
		left: 0;
		margin: 0;
		width: 30px;
		background-image: none;
		border-radius: 0;
	}

	.nav-links {
		flex-direction: column;
	}

	.header {
		margin-top: 20rem;
	}

	.title {
		font-size: 42px;
	}

	.sub-title {
		font-size: 14px;
	}

	h2 {
		font-size: 32px !important;
	}

	h3 {
		font-size: 24px !important;
	}

	.story {
		margin-top: 250px;
	}

	.wed-info {
		margin-top: 250px;
		margin-bottom: 250px;
	}

	.venue-image {
		width: 17rem;
		height: 17rem;
	}

	.full-screen-background {
		border: 75px solid transparent;
		border-image: url('./assets/img/bg_img.jpg') 500 stretch;
		overflow-y: scroll;
		overflow-x: hidden;
	}
}
